html {
    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
    font-size: 10px;
    line-height: 1.4;
}

.title {
    font-size: 5rem;
    color: #ffffff;
    font-weight: 600;
}

.subtitle {
    font-size: 1.8rem;
    color: #999999;
}

.intro {
    display: flex;
    z-index: 10;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #111111;
    transition: all 1s cubic-bezier(0.23, 1, 0.320, 1)
}

.flex {
    display: flex;
}

.none {
    display: none;
}

.logo {
    width: 20vw;
    animation: logo 2s cubic-bezier(0.645, 0.045, 0.355, 1);
    animation-iteration-count: 2;
}

.text-intro {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    opacity: 0;
    transition: all 3s cubic-bezier(0.23, 1, 0.320, 1);
}

.text-intro.visible {
    opacity: 1;
    transition: all 3s cubic-bezier(0.23, 1, 0.320, 1);
}

@media (max-width: 767px) {
    .logo {
        width: 40vw;
    }
}

@keyframes logo {
    0% { opacity: 0;

    }

    50% { opacity: 1;

    }

    100% { opacity: 0;

    }
}





